.arrow-right {

    width: 60px;

    height: 60px;

    background-color: transparent; /* 模块背景为透明 */

    border-color: #00FFFF;

    border-style: solid;

    border-width: 8px 8px 0 0;

    transform: translateX(-33px) rotate(45deg); /*箭头方向可以自由切换角度*/

}

.arrow-left {

    width: 60px;

    height: 60px;

    background-color: transparent; /* 模块背景为透明 */

    border-color: #00FFFF;

    border-style: solid;

    border-width: 8px 8px 0 0;

    transform: translateX(33px) rotate(-135deg); /*箭头方向可以自由切换角度*/

}